import * as React from 'react';
import { PageProps } from 'gatsby';
import { about as aboutData } from '../../content/intl/ja.json';

interface AboutProps extends React.HTMLProps<HTMLDivElement> {}

export default class extends React.Component<AboutProps, any> {
  public render() {
    return (
      <section id='about' className='bg-gray'>
        <div className='container'>
          <h1>{aboutData.title}</h1>
          <div className='about-background bg-white'>
            <div className='row'>
              <div className='col-10 offset-1 col-lg-8 offset-lg-2'>
                <table className='table'>
                  <tbody>
                    {aboutData.items.map((d: any, i: number) => (
                      <tr key={i}>
                        <th scope='row'>{d.key}</th>
                        <td>
                          <ul>
                            {typeof d.value === 'string' ? (
                              <li>{d.value}</li>
                            ) : (
                              d.value.map((text: string, i: number) => (
                                <li key={i}>{text}</li>
                              ))
                            )}
                          </ul>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
            <iframe
              src='https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3241.039999254714!2d139.70796711568818!3d35.67601708019551!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x60188cbdbe52e173%3A0x72c6f0f55ca53328!2z44CSMTUxLTAwNTEg5p2x5Lqs6YO95riL6LC35Yy65Y2D6aeE44Kx6LC377yS5LiB55uu77yR77yQ4oiS77yV!5e0!3m2!1sja!2sjp!4v1594057747524!5m2!1sja!2sjp'
              width='1200'
              height='360'
              frameBorder='0'
              allowFullScreen={false}
              aria-hidden='false'
              tabIndex={0}
            />
          </div>
        </div>
      </section>
    );
  }
}
