import * as React from 'react';
import { Helmet } from 'react-helmet';
import Layout from '../components/layout';
import OpeningAnimation from '../components/OpeningAnimation';
import Mainvisual from '../components/Mainvisual';
import Service from '../components/Service';
import About from '../components/About';
import Contact from '../components/Contact';
import Footer from '../components/Footer';
import * as data from '../../content/intl/ja.json';

interface IndexPageProps {
  data: {
    site: {
      siteMetadata: {
        siteUrl: string;
        ogpUrl: string;
      };
    };
  };
}

export default class extends React.Component<IndexPageProps, {}> {
  constructor(props: IndexPageProps, context: any) {
    super(props, context);
  }

  public render() {
    const { siteMetadata } = this.props.data.site;

    return (
      <Layout>
        <Helmet>
          <html lang={data.lang} />
          <meta name='description' content={data.description} />
          <title>{data.title}</title>
          <link rel='canonical' href={siteMetadata.siteUrl} />
          <meta property='og:title' content={data.title} />
          <meta property='og:type' content='website' />
          <meta property='og:url' content={siteMetadata.siteUrl} />
          <meta property='og:description' content={data.description} />
          <meta property='og:locale' content={data.locale} />
          <meta property='og:site_name' content={data.site_name} />
          <meta property='og:image' content={siteMetadata.ogpUrl} />
          <meta property='og:image:secure_url' content={siteMetadata.ogpUrl} />
          <meta property='og:image:type' content='image/png' />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:image:alt' content={data.site_name} />
          <meta name='twitter:card' content='summary_large_image' />
          <meta name='twitter:title' content={data.title} />
          <meta name='twitter:description' content={data.description} />
          <meta name='twitter;image' content={siteMetadata.ogpUrl} />
        </Helmet>
        <OpeningAnimation />
        <Mainvisual />
        <Service />
        <About />
        <Contact />
        <Footer />
      </Layout>
    );
  }
}

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        siteUrl
        ogpUrl
      }
    }
  }
`;
