import * as React from 'react';
import { PageProps } from 'gatsby';

const images: any[] = [];
images.push(require('../images/seen1.svg'));
images.push(require('../images/seen2.svg'));
images.push(require('../images/seen3.svg'));
images.push(require('../images/seen4.svg'));

interface OpeningAnimationProps extends React.HTMLProps<HTMLDivElement> {}

interface OpeningAnimationState {
  loadedCount: number;
  isLoadedComplete: boolean;
}

export default class extends React.Component<
  OpeningAnimationProps,
  OpeningAnimationState
> {
  constructor(props) {
    super(props);

    this.state = {
      loadedCount: 0,
      isLoadedComplete: false
    };

    this.handleLoadImage = this.handleLoadImage.bind(this);
    this.updatePosition = this.updatePosition.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.updatePosition);

    setTimeout(() => {
      this.updatePosition();

      this.setState({
        isLoadedComplete: true
      });
    }, 500);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updatePosition);
  }

  handleLoadImage() {
    return;
    let { loadedCount }: { loadedCount: number } = this.state;
    loadedCount++;

    this.setState({
      loadedCount
    });

    if (loadedCount === images.length) {
      // 
      setTimeout(() => {
        this.updatePosition();

        this.setState({
          isLoadedComplete: true
        });
      }, 500);
    }
  }

  updatePosition() {
    images.map((_, i: number) => {
      const $seen: any = document.querySelector(
        `.opening-animation__seen-${i + 1}`
      );
      $seen.style.top =
        (document.documentElement.clientHeight -
          $seen.getBoundingClientRect().height) /
          2 +
        'px';
    });
  }

  public render() {
    return (
      <div
        className='opening-animation'
        style={{
          opacity: this.state.isLoadedComplete ? 0 : 1,
          pointerEvents: this.state.isLoadedComplete ? 'none' : 'auto'
        }}
      >
        {images.map((img: any, i: number) => (
          <img
            key={i}
            className={`opening-animation__seen opening-animation__seen-${
              i + 1
            }`}
            onLoad={this.handleLoadImage}
            src={img}
            style={{
              opacity: this.state.isLoadedComplete ? 1 : 0
            }}
          />
        ))}
      </div>
    );
  }
}
